// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import Vuex from "vuex";
import storeConfig from "./vuex/store/store";
import VueCookie from "vue-cookie";
import { InfiniteScroll } from "mint-ui";
import MintUi from "mint-ui";
// import VuePopup from '@xunlei/vue-popup'
import { getopenid } from './assets/wechat';
getopenid();
Vue.use(Vuex);
// Vue.use(VuePopup)
Vue.use(VueCookie);
Vue.use(InfiniteScroll);

Vue.config.productionTip = false;
Vue.directive("focus", {
  // 当绑定元素插入到 DOM 中。
  inserted: function(el) {
    // 聚焦元素
    el.focus();
  }
});

const store = new Vuex.Store({
  state: {
    userID: -1,
    secretkey: "",
    userPhone: "",
    isclear: true,
    caldendar: false,
    traveltype: "go",
    bookingDetailRequests: ["1", "2"],
    peopleinfo: [],
    fromplace: "常州",
    toplace: "北京"
  },
  mutations: {
    addpeople(state, people) {
      //添加个人信息
      state.peopleinfo.push(people);
    },
    changepeople(state, people, index) {
      Vue.set(state.peopleinfo, 0, people);
      console.log("store", index);

      // state.peopleinfo.
    },
    pushbookingDetailRequests(state, planeinfo) {
      //返程
      state.bookingDetailRequests.push(planeinfo);
    },
    initbookingDetailRequests(state, planeinfo) {
      //去程
      state.bookingDetailRequests = [planeinfo];
    },
    changetraveltype(state, type) {
      state.traveltype = type + "";
    },
    disableStatus(state) {
      state.isclear = false;
    },
    enableStatus(state) {
      state.isclear = true;
    },
    setcercet(state, sccc) {
      state.secretkey = sccc;
    },
    setuserId(state, id) {
      state.userID = id;
    },
    setUserPhone(state, phone) {
      state.userPhone = phone;
    },
    setFromplace(state, pl) {
      state.fromplace = pl;
    },
    setToplace(state, pl) {
      state.toplace = pl;
    }
  }
});
router.beforeEach((to, from, next) => {
  switch (to.path) {
    case "/o":
    case "/pay":
    case "/searchticket":
    case "/orderinfo":
    case "/list":
      console.log("需要登陆");
      let name = VueCookie.get("gdmobileusername");
      let phone = VueCookie.get("gdmobileuserphone");
      let serc = VueCookie.get("usersecret");
      if(name==""||name==null||phone==""||phone==null||serc==""||serc==null){
        router.push("/login");
        MintUi.Toast("请先登录");
      }else{
        next();
      }
      break;
    default:
      console.log("不需要登陆");
      next();
      break;
  }
});
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  template: "<App/>",
  components: { App }
});



