<template>
  <div class="pay">
    <mt-heard class="pay-title" fixed title="订单支付">
      <mt-button icon="back" @click="clicktoBack" slot="left"></mt-button>
    </mt-heard>
    <div class="content">
      <div class="orderinfo">
        <div class="ortotalinfo">
          <p>在线支付:￥{{price}}</p>
          <!-- <p>剩余支付时间:00:30:00:00</p> -->
        </div>
        <div class="orderdetaile">
          <div class="line" v-for="(item,index) of planeinfo" :key="index">
            <div class="item">
              <div class="tip">{{item.flightTripType=='DEPARTURE'?'去程':'返程'}}:</div>
              <div class="cc">{{item.flightInfoVos[0].departureAirport.cityName}}-{{item.flightInfoVos[0].arrivalAirport.cityName}}</div>
            </div>
            <div class="item">
              <div class="tip">航班舱位:</div>
              <div class="cc">{{item.flightInfoVos[0].flightNo}} {{item.flightInfoVos[0].seats[0].seatClassName}}</div>
            </div>
            <div class="item">
              <div class="tip">航班日期:</div>
              <div class="cc">{{item.flightInfoVos[0].departureTime}}</div>
            </div>
          </div>
          <div class="orderpeople">
            <div class="item">
              <div class="tip">乘机人:</div>
              <div class="cc">
                <span v-for="(item,index) of peopleinfo" :key="index">
                  {{item.passengerName}}
                </span>
              </div>
            </div>
            <div class="item">
              <div class="tip">订单金额:</div>
              <div class="cc">￥{{price}}(在线支付)</div>
            </div>
          </div>
        </div>
      </div>
      <div class="paytype">
        <div class="payit" @click="clickToPay">
          <!-- <div class="im">
                        <icon name='wechat' scale="3.5"></icon>
                    </div> -->
          <div class="typetitle">
            立即支付
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Cookie from "vue-cookie";
import Icon from "vue-svg-icon/Icon.vue";
import { Header, Field, Popup, Button, Indicator, Radio } from "mint-ui";
export default {
  name: "pay",
  components: {
    "mt-heard": Header,
    "mt-button": Button,
    "mt-radio": Radio,
    icon: Icon
  },
  data() {
    return {
      totaleorderinfo:{},
      openid: "",
      mainorderno: "",
      paytype: "",
      price: 0,
      planeinfo: [],
      peopleinfo: [],
      out_trade_no: "",
      sign: "",
      guestIp: "",
      where: "11",
      userphone: "",
      secret: "",
      userid: ""
    };
  },
  created() {
    this.openid = this.$cookie.get("WXOPID");
    this.userphone = this.$cookie.get("gdmobileuserphone");
    this.secret = this.$cookie.get("usersecret");
    this.userid = this.$cookie.get("USERIDGDLY");
    this.where = this.$cookie.get("WHERE");
  },
  methods: {
    clicktoBack() {
      this.$router.go(-1);
    },
    clickToPay() {
      console.log("this.$store.state.userID", this.$cookie.get("USERIDGDLY"));
      console.log("支付:", this.mainorderno);
      console.log("this.out_trade_no", this.out_trade_no);
      console.log("this.out_trade_no", this.sign);
      console.log("this.out_trade_no", this.guestIp);
      let gd_reutrnurl =
        "http://m.czgdly.com/transportation/pay/html/airplanepaysuccess.html";
      let wd_returnurl =
        "http://m.wandaly.net/transportation/pay/html/airplanepaysuccess.html";
      let returnurl = "";
      // if (location.hostname == "m.wandaly.net") {
      //   returnurl = wd_returnurl;
      // } else {
      //   returnurl = gd_reutrnurl;
      // }
      returnurl=`http://${window.location.host}/transportation/pay/html/airplanepaysuccess.html`

      // console.log("获取订单的状态", response);
          let createdate = Date.parse(new Date(this.totaleorderinfo.insertDate));
          let current = Date.parse(new Date());
          console.log(createdate)
          console.log(current)
          if (current - createdate > 300000) {
            alert("超出支付时间,请重新下单");
            console.log("超时了");
          } else {
            BC.err = function(data) {
              //注册错误信息接受
              alert(data["ERROR"]);
            };
            BC.click(
              {
                title: "光大国旅飞机票",
                amount: parseInt(this.price * 100),
                out_trade_no: this.out_trade_no,
                sign: this.sign,
                return_url: returnurl, //http://m.czgdly.com/airplane/FlightTickets/#/list
                optional: {
                  customerId: this.$cookie.get("USERIDGDLY"),
                  customerSource: this.where,
                  mainOrderNo: this.mainorderno
                },
                analysis: {
                  ip: this.guestIp
                },
                openid: this.openid
              },
              {
                wxJsapiFinish: function(res) {
                  //jsapi接口调用完成后
                  // alert(JSON.stringify(res));
                  if (res.err_msg == "get_brand_wcpay_request:ok") {
                    window.location.href = returnurl;
                  }
                }
              }
            );
            BC.err = function(err) {
              //err 为object, 例 ｛”ERROR“ : "xxxx"｝;
              console.log(err);
            };
          }
      
    }
  },
  beforeRouteEnter: (to, from, next) => {
    console.log("支付页面", to.query);
    Indicator.open();
    (async () => {
      const where = Cookie.get("WHERE");
      const userid = Cookie.get("USERIDGDLY");
      const sercert = Cookie.get("usersecret");
      const userphone = Cookie.get("gdmobileuserphone");
      const getorderinfo = (orderno, userid, secret, phone, where) => {
        return axios.get(
          `http://58.216.175.118:86/api/LvmamaFlightTickets/FLocalOrders/GetFLOrderByNo/${orderno}?token=${userid}|${secret}|${phone}|${where}`
        );
      };
      const res_orderinfo = await getorderinfo(
        to.query.orderNo,
        userid,
        sercert,
        userphone,
        where
      );
      const _orderinfo = res_orderinfo.data;
      console.log("res_orderinfo", _orderinfo);

      const gd_paysignurl =
        "http://m.czgdly.com/transportation/airplane/Beecloud/getsign.asp"; //光大的
      const wd_paysignurl =
        "http://m.wandaly.net/transportation/gdtrain/Beecloud/getsign.asp"; //万达的
      let paysignurl = "";
      if (location.hostname == "m.wandaly.net") {
        paysignurl = wd_paysignurl;
      } else {
        paysignurl = gd_paysignurl;
      }
      const amount = _orderinfo.needPayPrice * 100;
      const title = "光大国旅飞机票";
      const orderno = _orderinfo.salesMainOrderNo;
      let getIp = function() {
        return axios.get(
          "http://m.czgdly.com/transportation/airplane/beecloud/getCurrentIp.asp"
        );
      };
      let getSign = function(t_title, t_orderno, t_price) {
        return axios.get(paysignurl, {
          params: {
            title: t_title,
            amount: parseInt(t_price), //amount
            orderno: t_orderno
          }
        });
      };
      const [res_ip, res_sig] = await axios.all([
        getIp(),
        getSign(title, orderno, amount)
      ]);
      Indicator.close();
      next(vm => {
        vm.mainorderno = _orderinfo.mainOrderNo;
        vm.planeinfo = _orderinfo.itemOrders;
        vm.peopleinfo = _orderinfo.orderPassengers;
        vm.price = _orderinfo.needPayPrice;
        vm.out_trade_no = _orderinfo.salesMainOrderNo;
        vm.sign = res_sig.data;
        vm.guestIp = res_ip.data;
        vm.totaleorderinfo=_orderinfo;
      });
    })();

    // axios.all([getIp(), getSign(title, orderno, amount)]).then(
    //   axios.spread(function(ip, sig) {
    //     console.log("支付页面的");
    //     console.log("ip", ip.data);
    //     console.log("sign", sig);
    //     Indicator.close();
    //     next(vm => {
    //       vm.mainorderno = to.query.marinorderno;
    //       vm.planeinfo = to.query.plane;
    //       vm.peopleinfo = JSON.parse(to.query.passenger);
    //       vm.price = to.query.price;
    //       vm.out_trade_no = to.query.orderno;
    //       vm.sign = sig.data;
    //       vm.guestIp = ip.data;
    //     });
    //   })
    // );
    // axios
    //   .get("http://m.czgdly.com/airplane/Beecloud/getsign.asp", {
    //     params: {
    //       title: title,
    //       amount: 1, //amount
    //       orderno: orderno
    //     }
    //   })
    //   .then(response => {
    //     //获取支付的相关的的东西

    //     console.log("response", response);

    //     Indicator.close();
    //     next(vm => {
    //       vm.mainorderno = to.query.marinorderno;
    //       vm.planeinfo = to.query.plane;
    //       vm.peopleinfo = to.query.passenger;
    //       vm.price = to.query.price;
    //       vm.out_trade_no = to.query.orderno;
    //       vm.sign = response.data;
    //     });
    //   })
    //   .catch(response => {});
  }
};
</script>

<style scoped>
.pay-title {
  background-color: #fff0;
}
.orderinfo {
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
}
.content {
}
.pay {
  /* padding-top: 45px; */
  padding: 45px 10px 45px 10px;
}
.orderdetaile {
  margin-top: 10px;
  color: #666;
}
.orderdetaile .item {
  display: flex;
  flex-direction: row;
}
.orderdetaile .item .tip {
  width: 30%;
}
.orderdetaile .item .cc {
  width: 70%;
}
.ortotalinfo {
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
}
.orderpeople {
  margin-top: 10px;
}
.paytype {
  margin-top: 10px;
}
.payit {
  color: #fff;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  background-color: orange;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.payit .im {
  width: 20%;
}
</style>



