<template>
  <div class="app">
    <transition name="fade">
    <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: "app"
};
</script>

<style >
* {
  padding: 0;
  margin: 0;
}
html,body{
    width:100%;
    height:100%;
}
body {

  background-image: url(https://pic.c-ctrip.com/AssetCatalog/h5/swift/global-bg-new.jpg);
  background-size: 100% auto;
  background-color: #0666c1;
  background-repeat: no-repeat;
}
.app{
  width: 100%;
  height: 100%;
}
/* 动画 */
.fade-enter-active {
  transition: all 0.5s ease;
}
.fade-leave-to {
  transform: translateY(-200px);
  opacity: 0;
}
.fade-enter {
  transform: translateY(500px);
  opacity: 0;
}
</style>



